import React from "react";
import my from "../Assets/my.png"
import "../styles/About.css";
import Animation from '../Data/animation'
import Lottie from 'react-lottie';
import { Button } from "@material-ui/core";


function About() {
  return (
  <div className="AboutContainer">
      <div className="AboutImg">
      <Lottie
	                 options={Animation.About}
                     height={ 500 }
                     width={ 500 }
                 />
        <div className="AboutBack">
        <img src={my} alt="not found" srcSet="" />
        <Lottie
	                 options={Animation.About}
                     height={ 300 }
                     width={ 300 }
                 /> 
        </div>
        <img src={my} alt="not found" srcSet="" />
        <div className="AboutTxt">
          <h1>Hello ,</h1>
          <h3>I'm Shivam Parve</h3>
          <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ab, debitis, obcaecati et culpa id possimus modi vero sequi minus consequuntur rem nemo ea illo similique mollitia, ex quisquam repellat. Dolores inventore a maiores! Debitis vero quis doloribus nihil culpa mollitia.</p>
          <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ab, debitis, obcaecati et culpa id possimus modi vero sequi minus consequuntur rem nemo ea illo similique mollitia, ex quisquam repellat.</p>
        </div>
        <div className="CV">
        <Button 
          className="CV"
          color="primary"
          href="S.pdf"
          target="_blank" 
          download 
          > <h2>Download CV</h2></Button>
        </div>
      </div>
  </div>
  );
}

export default About;
