import { Button } from '@material-ui/core'
import React from 'react'
import '../styles/Skills.css'
import {skills,Bskills,Dskills} from '../Data/skill'


function Skills() {

    return (
        <div className="SkillsContainer">
            <div className="SkillsCont">
                <div className="Skillsheading">
                    <h1>Skills</h1>
                </div>

        {/* FrontEnd Tab */}
        
                <div className="frontEnd">
                    <h1 className="skillHeading"><u>Front End Skills :</u></h1>
                    <div className="cont">

                    {
                       skills.map(skill => (
                           <div className="skill">
                           <div className="lang">
                            <div className="logo">
                            {skill.svg}
                            </div>
                            <div className="name">
                                <h1> {skill.name}</h1>
                                <div className="range">
                                    <h1>{skill.range}%</h1>
                                    <div className="r" style={{width:skill.range +'%' }} > 
                                </div>
                           </div>
                            </div>
                           </div>
                           <Button><h1>See Projects</h1></Button>
                        </div>
                       ))
                    }

                    </div>
                </div>

            
        {/* BackEnd Tab */}

                <div className="frontEnd">
                    <h1 className="skillHeading"><u>Back End Skills :</u></h1>
                    <div className="cont">

                    {
                       Bskills.map(skill => (
                           <div className="skill">
                           <div className="lang">
                            <div className="logo">
                            {skill.svg}
                            </div>
                            <div className="name">
                                <h1> {skill.name}</h1>
                                <div className="range">
                                    <h1>{skill.range}%</h1>
                                    <div className="r" style={{width:skill.range +'%' }} > 
                                </div>
                           </div>
                            </div>
                           </div>
                           <Button><h1>See Projects</h1></Button>
                        </div>
                       ))
                    }                       
                    </div>
                </div>
            
        {/* Design Tab */}

                <div className="frontEnd">
                    <h1 className="skillHeading"> <u>Designing Skills :</u></h1>
                    <div className="cont">

                    {
                       Dskills.map(skill => (
                           <div className="skill">
                           <div className="lang">
                            <div className="logo">
                            {skill.svg}
                            </div>
                            <div className="name">
                                <h1> {skill.name}</h1>
                                <div className="range">
                                    <h1>{skill.range}%</h1>
                                    <div className="r" style={{width:skill.range +'%' }} > 
                                </div>
                           </div>
                            </div>
                           </div>
                           <Button><h1>See Projects</h1></Button>
                        </div>
                       ))
                    }                       
                    </div>
                </div>
            


            </div>
        </div>
    )
}

export default Skills
