import React from 'react';
import "../styles/WelcomeScreen.css"
import my from "../Assets/my.png"
import Lottie from 'react-lottie';
import Animation from '../Data/animation.js'
import { Link } from 'react-router-dom';



function WelcomeScreen() {

    return (
            <div className={"InsideContainer" } >
                <div  className="BackgroundAnim">
                <Lottie
	                 options={Animation.Background}
                     height={ 800 }
                     width={ 1500 }
                 /> 
                </div>
                <div className="CardsContainer">
                   
                    {/* Card - About  */}

                <Link to="/about" className="card" style={{ textDecoration: 'none' }} >
                    <div>
                        <div className="ImgContainer">
                            <img src={my} alt="" srcSet="" />
                        </div>
                        <div className="myDesc">
                            <h1>Hello,<br/><span> I'm Shivam Parve</span></h1>
                            <p>Student of BCS [BS.c Computer Science] at MGM'S College Of Computer Science And IT.<br/>
                            <br/>Application Programmer | Frontend Developer | Web , Web-App  Developer</p>
                        </div>
                        </div> 
                </Link>

                    {/* Card - Skills  */}  

                <Link to="/skills" className="card"  style={{ textDecoration: 'none' }}>
                                     <div className="insideCard" >
                            <div className="Anim1">
                                <Lottie 
	                                options={Animation.Skills}
                                    height={ 177 }
                                    width={ 260 }
                                />
                            </div>
                            <div className="Anim2">
                            <Lottie 
	                            options={Animation.Skills}
                                height={ 130 }
                                width={ 200 }
                            />
                            </div>
                            <div className="SkillsBox">
                                <h1><span>My Skills </span></h1> 
                                <br />
                                <h2>Front-End Skills : </h2>
                                <p>HTML-5 | CSS-3 | Javascript | React JS | React Native | Material UI </p>
                                <br />
                                <h2>Back-End Skills : </h2>
                                <p>Python | Javascript | C, C++ | Django | Firebase</p>
                           </div>
                        </div>
                </Link>
                    {/* Card - Projects  */}

                <Link to="/projects" className="card" style={{ textDecoration: 'none' }}>


                   <div className="insideCard" >
                            <div className="Anim1">
                            <Lottie 
	                            options={Animation.Project}
                                height={ 177 }
                                width={ 260 }
                            />
                            </div>
                            <div className="Anim2">
                            <Lottie 
	                            options={Animation.Project}
                                height={ 140 }
                                width={ 200 }
                            />
                            </div>
                            

                            <div className="ProjectsBox">
                                <h1> <span>My Projects </span></h1>
                                <br />
                                <h2>Web Development :</h2>
                                <p>- Local Travel Guide </p>
                                <p>- E-Market place</p>
                                <br />
                                <h2>Mobile Applications :</h2>
                                <p>- Social Chat App</p>
                                <p>- Cab Booking System</p>
                            </div> 
                        </div>               
                </Link>

                    {/* Card - Contact  */}
                
                <Link to="/contact" className="card" style={{ textDecoration: 'none' }} >

                   <div className="insideCard" >
                            <div className="Anim1">
                            <Lottie 
	                            options={Animation.Contact}
                                height={ 170 }
                                width={ 200 }
                            />
                            </div>
                            <div className="Anim2">
                            <Lottie 
	                            options={Animation.Contact}
                                height={ 150 }
                                width={ 200 }
                            />
                            </div>
                            <div className="ContactBox">
                                <h1><span>Reach Me </span></h1>
                                <br />
                                <h2>E-mail : </h2>
                                <p>shivamparve777@gmail.com </p>
                                <p>shivam.parve@outlook.com</p>
                                <h2>Mobile :</h2>
                                <p>+91 7887578712</p>
                                <h2>GitHub :</h2>
                                <p>https://github.com/shivamparve</p>
                            </div>                        
                        </div>
                </Link>       
                </div>
            </div>
    )
}

export default WelcomeScreen;