import React from 'react'
import "../styles/Contact.css"
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import TelegramIcon from '@material-ui/icons/Telegram';
import { Button } from '@material-ui/core'
import Animation from '../Data/animation'
import Lottie from 'react-lottie';
import PersonPinCircleIcon from '@material-ui/icons/PersonPinCircle';
import PhoneIcon from '@material-ui/icons/Phone';
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';


const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
      width: '50ch',
    },
  },
}));

function Contact() {
    const classes = useStyles();
  const [value, setValue] = React.useState('Controlled');

  const handleChange = (event) => {
    setValue(event.target.value);
  };
    return (
        <div className="ContactCont" >
            <div className="innerCont">
            <div className="contactHeading">
                <h1>Let's Talk</h1>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Harum consectetur quod repudiandae totam? Lorem ipsum, dolor sit amet consectetur adipisicing elit. Cupiditate consequuntur unde quas!</p>
            </div>
            <div className="cForm">
            <form className={classes.root} noValidate autoComplete="off">
            <div className={classes.margin}  >
        <Grid className="ip" container spacing={1} alignItems="flex-end">
          <Grid item>
            <AccountCircle className="contIcon" fontSize="large" />
          </Grid>
          <Grid item>
            <TextField id="input-with-icon-grid" label="Your Name" />
          </Grid>
        </Grid>
        <Grid className="ip" container spacing={1} alignItems="flex-end">
          <Grid item>
            <MailIcon className="contIcon" fontSize="large" />
          </Grid>
          <Grid item>
            <TextField id="input-with-icon-grid" label="Your E-mail" />
          </Grid>
        </Grid>
        <Grid className="ip" container spacing={1} alignItems="flex-end">
          <Grid item>
            <TelegramIcon className="contIcon msg" fontSize="large" />
          </Grid>
          <Grid item>
            <TextField id="input-with-icon-grid" label="Your Message" 
          variant="outlined"
          multiline
          maxRows={4}
          onChange={handleChange}/>
          </Grid>
        </Grid>
      </div>
         </form>
            </div>
            <div className="submitbtn">
            <Button  color="primary">
              <h1>Send Message</h1>
            </Button>
            </div>
            </div>
            <div className="innercont2">
              <div className="contAnim">
              <Lottie
	                 options={Animation.Cont}
                     height={ 400 }
                     width={ 450 }
                 /> 
              </div>
              <div className="myConnect">
                <div className="mycont">
                <PersonPinCircleIcon className="contIcon" fontSize="large" />
                <p>Pimpri-Chinchwad, Pune, Maharashtra, India. </p>
                </div>
                <div className="mycont">
                <PhoneIcon fontSize="large" className="contIcon" />
                <p>+91 7887578712 </p>
                </div>
                <div className="mycont">
                <MailIcon className="contIcon" fontSize="large" />
                <p>shivamparve@gmail.com </p>
                <p>|  shivam.parve@outlook.com</p>
                </div>
             </div>
             <div className="mySocials">
                <InstagramIcon className="social1" fontSize="large"/>
                <FacebookIcon className="social2" fontSize="large"/>
                <GitHubIcon style={{cursor:'pointer'}} fontSize="large"/>
             </div>
            </div>
        </div>
    )
}

export default Contact
