import skills from '../Assets/skills.json'
import project from '../Assets/project.json'
import contact from '../Assets/contact.json'
import nural from '../Assets/NuralCycle.json'
import background from '../Assets/welcomeBack.json'
import about from '../Assets/about.json'
import mobnav from '../Assets/mobnav.json'
import cont from '../Assets/cont.json'
import React from 'react';


var Animation = {

  NuralCycle:{
    loop: true,
    autoplay: true,
    animationData: nural,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  },
  Skills: {
    loop: true,
    autoplay: true,
    animationData: skills,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  },
  
  Contact: {
    loop: true,
    autoplay: true,
    animationData: contact,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  },

  Project: {
    loop: true,
    autoplay: true,
    animationData: project,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  },
  Background: {
    loop: true,
    autoplay: true,
    animationData: background,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  },
  About: {
    loop: true,
    autoplay: true,
    animationData: about,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  },
  
  Cont: {
    loop: true,
    autoplay: true,
    animationData: cont,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  },
  MobNav: {
    loop: true,
    autoplay: true,
    animationData: mobnav,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  }

};

export default Animation;
