import React, { useState } from "react";
import "../styles/Nav.css";
import PersonIcon from "@material-ui/icons/Person";
import SchoolSharpIcon from "@material-ui/icons/SchoolSharp";
import ContactsIcon from "@material-ui/icons/Contacts";
import CodeIcon from "@material-ui/icons/Code";
import { Link } from 'react-router-dom';
import Animation from "../Data/animation";
import Lottie from 'react-lottie';


function Nav() {

  const [About, setAbout] = useState(false)
  const [Skills, setSkills] = useState(false)
  const [Projects, setProjects] = useState(false)
  const [Contact, setContact] = useState(false)

  

  return (
   <div className="nav" >  
   
      <ul className="navList">
      <div className="MobNav">
   <Lottie
	                 options={Animation.MobNav}
                     height={ 360 }
                     width={ 450 }
                 />
   </div>
        <Link to='/about' style={{ textDecoration: 'none' }}>
        <li className={!About ? "navListItem" : "navListItem navListActive"} onClick={()=>{setSkills(false)&&setProjects(false)&&setContact(false)&&setAbout(true)}} >
          <PersonIcon fontSize="large" className="menuIcon" />
          <h1>About</h1>
        </li>
        </Link>
        <Link to='/skills' style={{ textDecoration: 'none' }}> 
        <li className={!Skills ? "navListItem" : "navListItem navListActive"} onClick={()=>{setAbout(false)&&setProjects(false)&&setContact(false)&&setSkills(true)}} >
          <SchoolSharpIcon fontSize="large" className="menuIcon" />
          <h1>Skills</h1>
        </li>
        </Link>
        <Link to='/projects' style={{ textDecoration: 'none' }}>
        <li className={Projects ? "navListItem navListActive" : "navListItem" } onClick={()=>{setSkills(false)&&setAbout(false)&&setContact(false)&&setProjects(true)}} >
          <CodeIcon fontSize="large" className="menuIcon" />
          <h1>Projects</h1>
        </li>
        </Link>
        <Link to='/contact' style={{ textDecoration: 'none' }}>
        <li className={!Contact ? "navListItem" : "navListItem navListActive"} onClick={()=>{setSkills(false)&&setProjects(false)&&setAbout(false)&&setContact(true)}} >
          <ContactsIcon fontSize="large" className="menuIcon" />
        <h1>Contact</h1>
        </li> 
        </Link>
      </ul>
    </div>
  );
}

export default Nav;
